import React, { useRef, useEffect } from 'react';
import Chart from 'chart.js/auto';

type LineChartProps = {
  data: number[];
  labels: string[];
  typeChart: 'line' | 'bar' | 'radar'
  optionsCart?: 'x'|"y"
}


const LineChart: React.FC<LineChartProps> = ({ data, labels , typeChart}) => {
  const chartRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    if (chartRef.current) {
      const ctx = chartRef.current.getContext('2d');
      if (ctx) {
        new Chart(ctx, {
          type: typeChart,
          data: {
            labels: labels,
            datasets: [
              {
                label: 'Продажи',
                data: data,
                backgroundColor: 'rgba(75, 192, 192, 0.2)',
                borderColor: 'rgba(75, 192, 192, 1)',
                borderWidth: 1
              }
            ]
          },
          options: {
            indexAxis:"y",
            responsive: true,
            maintainAspectRatio: false
          }
        });
      }
    }
  }, [data, labels, typeChart]);

  return <canvas ref={chartRef} />;
};

export default LineChart;
